.text-transform-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 2rem;
}

.input-section {
  margin-bottom: 1.5rem;
}

textarea {
  width: 100%;
  padding: 1rem;
  border: 2px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  resize: vertical;
  min-height: 100px;
}

textarea:focus {
  outline: none;
  border-color: #007bff;
}

.button-section {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

button {
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  flex: 1;
  min-width: 120px;
}

button:hover {
  background-color: #0056b3;
}

.output-section {
  background-color: #f8f9fa;
  padding: 1.5rem;
  border-radius: 5px;
}

.output-section h3 {
  color: #333;
  margin-bottom: 1rem;
}

.transformed-text {
  padding: 1rem;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  min-height: 50px;
  word-wrap: break-word;
} 